.c-page-intro {
    @include text-dimensions($ms4,1.3,1.5);
    color: $color-alpha;
    max-width: 18em;
    margin-left: auto;
    margin-right: auto;

    @include mq(bp2) {
        @include text-dimensions($ms5,1.5);
    }

    @include mq(bp3) {
        @include text-dimensions($ms7,2);
    }

    @include mq(bp5) {
        margin-left: 0;
        margin-right: 0;
        @include text-dimensions($ms9,2.7,3);
        @include padding-right(2);
    }
}

.c-subtitle {
    color: palette(color-neutral,x-light);
    @include text-dimensions($ms9,2.7,0);
}
