/*
    Components: Cards
    ---
    A card displays site content in a manner similar to a playing card.
*/

.c-card {
    @include padding-tb(1,1);
    @include padding-lr(1,1);
    @include margin-bottom(1);
}
    // Reset bottom margins on children to make equal paddings possible
    .c-card > *:last-child,
    .c-card > *:last-child > *:last-child,
    .c-card > *:last-child > *:last-child > *:last-child {
        margin-bottom: 0;
    }

.c-card--alpha {
    color: palette(color-neutral,x-dark);
    background: repeating-linear-gradient(
        -45deg,
        transparent,
        transparent 4px,
        palette(color-neutral,x-light) 4px,
        palette(color-neutral,x-light) 6px
    );
}
