/*
    Components: Row
    ---
    Creates a horizontal row with padding
*/

.c-row {
    @include padding-tb($beta-leading,0);
    @include padding-lr($beta-leading,$beta-leading);

    @include mq(bp5) {
        @include padding-tb($beta-leading*1.5,0);
        @include padding-lr($beta-leading*2,$beta-leading*2);
    }
}

.c-row--large {
    @include padding-tb($beta-leading*2,(($beta-leading*2) - $alpha-leading));

    @include mq(bp3) {
        @include padding-tb($beta-leading*3,(($beta-leading*3) - $alpha-leading));
    }
}

.c-row--header {

}

.c-row--full {
    @include padding-tb(2);
    position: relative;
    text-align: center;

    @include mq(bp3) {
        @include padding-tb(10,2);
    }

    @include mq(bp4) {
        @include padding-tb(10,10);
    }

    @include mq(bp5) {
        text-align: left;
    }
}

    .c-row__content {

