    }

.c-row--alpha {
    background-color: palette(color-neutral,x-light);
}

.c-row--beta {
    position: relative;
    z-index: 2;
    color: palette(color-neutral,x-dark);
    background-color: tint(palette(color-alpha),80);

    & + .c-row--gamma,
    & + & {
        border-top: 0;
    }
}

.c-row--gamma {
    color: palette(color-neutral,x-dark);
    background-color: tint(palette(color-beta),85);
    border: 20px solid palette(color-neutral,x-light);

    & + .c-row--beta,
    & + & {
        border-top: 0;
    }
}

.c-row--delta {
    position: relative;
    color: palette(color-neutral,x-dark);
}
